<template>
  <v-container fluid>
    <v-layout column align-center class="testimonials">
      <v-flex xs12>
        <!-- <v-card> -->
          <v-container grid-list-md>
            <v-layout row wrap>
              <v-flex
                xs12
                sm6
                md6
                lg4
                xl2
                flex
                v-for="testimonial in testimonialsList"
                :key="testimonial.key"
              >
              <v-card light>
                <v-card-title primary-title>
                <div class="headline">{{testimonial.body}}</div>
                <!-- <div>{{testimonial.body}}</div> -->
              </v-card-title>
              <v-card-actions>
                <!-- <v-btn flat light>By Abc</v-btn> -->
                <span class="grey--text" text-xs-right>{{testimonial.name}}</span>
              </v-card-actions>
              </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        <!-- </v-card> -->
          <v-btn
            fab
            dark
            color="secondary"
            bottom
            right
            fixed
            @click.stop="dialog = !dialog"
            class="add-btn">
          <v-icon dark>add</v-icon>
        </v-btn>
        <v-dialog v-model="dialog" persistent width="800px">
         <v-card light>
          <v-card-title class="secondary title white--text">
            <div class="headline">Add Testimonial</div>
          </v-card-title>
          <v-container grid-list-sm class="pa-4">
            <v-layout row wrap>
              <v-flex xs12 align-center justify-space-between>
                <v-layout align-center>
                    <v-text-field
                    ref="name"
                    :rules="[() => !!form.name || 'This field is required']"
                    prepend-icon="person"
                    placeholder="Name"
                    required
                    v-model="form.name"
                  >
                  </v-text-field>
                </v-layout>
                <v-layout align-center>
                  <v-text-field
                  ref="email"
                  :rules="emailRules"
                  prepend-icon="email"
                  placeholder="Email"
                  required
                  v-model="form.email"
                >
                  </v-text-field>
                </v-layout>
                <v-layout align-center>
                  <v-text-field
                  ref="message"
                  prepend-icon="message"
                  placeholder="Message"
                  required
                  multi-line
                  :rules="[(v) => (v.length > 10 && v.length <= 500) || '10-500 characters allowed']"
                  :counter="500"
                  v-model="form.message"
                  >
                  </v-text-field>
                </v-layout>
                <vue-recaptcha
                  @verify="onVerify"
                  @expired="onExpired"
                  sitekey="'6LcDfVwUAAAAAMIVEMvCqsjEm3T8x9sa-QSbg_aD'"
                  ref="recaptcha"
                >
                </vue-recaptcha>
              </v-flex>
              </v-layout>
            </v-container>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="dialog = false">Cancel</v-btn>
                <v-btn flat color="secondary" @click="saveTestimonial">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>
import { TESTIMONIALS_REF } from '@/constants'
import { VDialog, VTextField } from 'vuetify'
import VueRecaptcha from 'vue-recaptcha'

export default {
  firebase: {
    testimonials: TESTIMONIALS_REF
  },
  components: {
    VDialog,
    VTextField,
    VueRecaptcha
  },
  data () {
    return {
      dialog: false,
      form: {
        email: '',
        message: '',
        name: ''
      },
      verified: false,
      formHasErrors: false,
      emailRules: [
        v => {
          return !!v || 'E-mail is required'
        },
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
      ]
    }
  },
  computed: {
    formIsValid () {
      return (
        this.form.name &&
        this.form.email &&
        this.form.message &&
        this.verified
      )
    },
    testimonialsList: {
      get () {
        return this.testimonials.filter(t => {
          return t.display
        })
      }
    }

  },
  methods: {
    saveTestimonial () {
      this.formHasErrors = false
      // this.$refs.recaptcha.execute()
      Object.keys(this.form).forEach(f => {
        if (!this.form[f]) this.formHasErrors = true
        this.$refs[f].validate(true)
      })
      if (this.formIsValid && !(this.$refs.name.hasError || this.$refs.email.hasError || this.$refs.message.hasError)) {
        TESTIMONIALS_REF.push({
          name: this.form.name,
          email: this.form.email,
          body: this.form.message,
          display: false
        })
        this.dialog = false
        this.form.email = ''
        this.form.name = ''
        this.form.message = ''
        this.verified = false
      }
    },
    onVerify (response) {
      // console.log('Verify: ' + response)
      this.verified = true
    },
    onExpired () {
      this.verified = false
    },
    hideForm () {
      this.dialog = false
    }
  }
}
</script>



